"use client";
import PrivateRoute from "../../../components/PrivateRoute";

export default function UserSubjects() {
  return (
    <PrivateRoute allowedRoles={["user"]}>
      <div style={{ padding: "20px" }}>
        <h1>Subjects List</h1>

        <ul>
          <li>Mathematics</li>
          <li>Physics</li>
          <li>Chemistry</li>
          <li>Computer Science</li>
        </ul>
      </div>
    </PrivateRoute>
  );
}
